import uniRate from '@/components/CTKY/StarJudge/uni-rate/uni-rate.vue'
export default {
	components:{uniRate},
	data() {
		return {
			textareaValue:""
		}
	},
	methods: {
		/**
		 * @name 获取textarea内容
		 */
		blur(e){
			this.infoReceive.textareaValue = e.detail.value
			console.log('输入框',this.textareaValue)
		},
		/**
		 * @name 提交
		 */
		submit(){
			this.$emit('submit',this.infoReceive.textareaValue)
			console.log('提交',this.textareaValue)
		},
		// 点击星星
		onchange(e) {
			// console.log('rate发生改变:' + JSON.stringify(e))
		}
	}
}
